.search-hotel {
  margin: 0 auto;
  padding: 28px 6.25% 49px;
  text-align: center;
}

.search-hotel__title {
  font-size: 21px;
  font-weight: 700;
  line-height: 26px;
  color: @black;
}

.search-hotel__text {
  margin-top: 19px;
  line-height: 24px;
}

.search-hotel__button {
  margin-top: 35px;
}

@media (min-width: @tablet-width) {
  .search-hotel {
    padding: 54px 0 58px;
    max-width: 458px;
  }

  .search-hotel__title {
    font-size: 30px;
    line-height: 30px;
  }

  .search-hotel__text {
    margin-top: 27px;
  }

  .search-hotel__button {
    margin-top: 42px;
  }
}

@media (min-width: @desktop-width) {
  .search-hotel {
    padding-bottom: 66px;
  }

  .search-hotel__button {
    margin-top: 47px;
  }
}
